* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body {
  background: linear-gradient(90deg, rgba(48, 16, 255, 1) 0%, rgba(100, 115, 255, 1) 100%);
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  width: 520px;
  min-height: 400px;
  background: #161a2b;

  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1{
  margin: 32px 0;
  color: #fff;
  font-size: 25px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-btn {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(90deg, rgba(93, 12, 255, 1)0%, rgba(155, 0, 250, 1)100%);
    color: #fff;
    text-transform: capitalize;
  }

.todo-form{
  margin-bottom: 32px;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(90deg, rgba(255, 118, 20, 1)0%, rgba(255, 84, 17, 1)100%);
  padding: 16px;
  border-radius: 5px;
  width: 80%;
}

.todo-row:nth-child(4n+1) {
  background: linear-gradient(90deg, rgba(93, 12, 255, 1)0%, rgba(155, 0, 250, 1)100%);
}

.todo-row:nth-child(4n+2) {
  background: linear-gradient(90deg, rgba(255, 12, 241, 1)0%, rgba(250, 0, 135, 1)100%);
}

.todo-row:nth-child(4n+3) {
  background: linear-gradient(90deg, rgba(20, 159, 255, 1)0%, rgba(17, 122, 255, 1)100%);
}

.icon{
  display: flex;
  align-items: center;
  font-size: 20px;
  cursor:pointer;
}

.detele {
  margin-right: 4px;
  color:#fff;
}

.edit {
  color: #fff;
}